.button__container {
  width: 200px;
  height: 200px;
  position: relative;
  border-radius: 10px;
  background-size: contain;

  &:not(:last-child) {
    margin-right: 90px;
  }

  .button__img {
    background-size: contain;
    width: 200px;
    height: 200px;
    border-radius: 10px;
    background: rgba(0, 0, 0, 0.35);

    &:hover {
      border: 4px solid #ffffff;
      border-radius: 10px;
    }
  }

  .button__svg {
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%);
  }
}
