.app__container {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0;
  // background-image: url("../public/images/sunny-day.jpg");
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  &--sun {
    background-image: url("../public/images/sunny-day.jpg");
  }

  &--snow {
    background-image: url("../public/images/snow.jpg");
  }

  &--rain {
    background-image: url("../public/images/rain.jpg");
  }
}

.h1__icon {
  margin-left: 10px;
  margin-right: 10px;
  margin-bottom: -10px;
}
